header {
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #7db9e8;
    background: -webkit-linear-gradient(left, #7db9e8 0%,#1e5799 100%);
    background: linear-gradient(to right, #7db9e8 0%,#1e5799 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#7db9e8', endColorstr='#1e5799',GradientType=1 );
}

.completed-statistic {
    color: #77dfff;
}

.top-block {
    display: flex;
}

.top-block input {
    flex-grow: 1;
    padding: 0 15px;
    border: 1px solid #007bff;
    border-radius: 4px;
    margin-right: 15px;
}
.todo-list-item {
    display: flex;
    align-items: center;
}

.todo-list-item span {
    flex-grow: 1;
    cursor: pointer;
}

.todo-list-item button {
    width: 30px;
    border-color: #007bff;
}

.todo-list-item button:not(:last-child) {
    margin-right: 5px;
}

.todo-list-item button:hover,
.todo-list-item button:active {
    background-color: #007bff !important;
    border-color: #007bff !important;
}

.todo-list-item button i {
    color: #007bff;
}

.todo-list-item button:hover i,
.todo-list-item button:active i {
    color: #fff;
}

.todo-list-item.done span {
    text-decoration: line-through;
}

.todo-list-item.important span {
    color: steelblue;
    font-weight: bold;
}
.todo-list {
    margin-top: 20px;
}

.todo-list .list-group-item {
    padding: 5px 15px;
}

.todo-list .list-group-item:not(:last-child) {
    margin-bottom: 5px;
}
.add-block {
    margin-top: 20px;
    display: flex;
}

.add-block input {
    border-color: #007bff;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    height: auto;
    padding: 0 15px;
}

.add-block button {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.wrapper {
    max-width: 500px;
    margin: 50px auto 0;
}

main {
    border: 1px solid #c2c6ce;
    border-top: none;
    padding: 30px 15px 15px;
}

a,
button,
input,
.btn {
    box-shadow: none !important;
    outline: none;
}
