.add-block {
    margin-top: 20px;
    display: flex;
}

.add-block input {
    border-color: #007bff;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    height: auto;
    padding: 0 15px;
}

.add-block button {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}